*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html{
  font-family: 'Open Sans', sans-serif;
  font-size: 16px;
}
div.app{
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    min-height: 100vh;
    width: 90%;
    background-color: #eee;
    color: 222;
}
header{
    flex-basis: 30vh;
    background-color: #000;
}
main{
    display: flex;
    padding: 30px 10px ;
    background-color: #ddd;
    flex-grow: 1;
}

aside{
min-width: 240px;
/* background-color: #999; */
}

section.page{
    /* background-color: royalblue; */
    flex-grow: 1;
}


footer{
    background-color: #000;
    color: white;
    text-align: center;
    text-transform: uppercase;
}
header{
    position: relative;
    overflow: hidden;
}
header img{
    position: absolute;
    top:50%;
    left: 50%;
    transform: translate(-50%,-50%);
    min-width: 100%;
    min-height:100% ;

}
nav.main{
    background-color: #ddd;
    padding: 0 30px 30px 0;
}

.main ul li{
    list-style: none;
    cursor: pointer;
}

nav.main a {
    display: block;
    text-decoration: none;
    color: black;
    text-transform: uppercase;
    font-weight:bold ;
    padding: 10px;
    transition: .2s;
    
}
nav.main a:hover{
    /* border-left: 10px solid red; */
    padding-left: 20px;
}

nav.main a.active{
    border-left: 10px solid red;
    padding-left: 20px;
}
nav.main li:nth-child(1) a.active{
    border-left: 10px solid green;
    
}
nav.main li:nth-child(2) a.active{
    border-left: 10px solid firebrick;
    
}
nav.main li:nth-child(3) a.active{
    border-left: 10px solid blue;
    
}
nav.main li:nth-last-child(1) a.active{
    border-left: 10px solid red;
    
}
.contact textarea{
    font-size: 16px;
    min-height: 200px;
    width:80%;
    margin:20px 0;
    line-height: 1.6;
}
.contact button{
    display: block;
    margin: 0 20% 0 auto;
    border: 2px solid black;
    background-color: white;
    padding: 10px 20px;
    font-size: 16px;
    transition: .3s;
}

.contact button:hover{
    color: white;
    background-color: #000;
}
.products h2{
    margin-bottom: 20px;
}
.products ul{
    
    padding-left: 30px;
}
.products li{
    margin-bottom: 20px;
    list-style: square;
    cursor: pointer;
}
.products a{
    text-decoration: none;
}

.products a:hover{
    text-decoration:underline;
    color:black ;
}
footer span{
    color: red;
}
footer p{
    font-size: 13px;
}
